* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  list-style-position: inside;
}

a {
  color: #007bff;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 10px;
}

.hover {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  transition: box-shadow .25s;
}

.hover:hover {
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 3000;
}

.btn-up {
  position: fixed;
  color: white;
  right: 5px;
  bottom: 50px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  display: none;
  z-index: 500;
  opacity: 0.6;
  transition: 0.5s;
  z-index: 9999;
}

.btn-up img {
  width: 100%;
}

.btn-up:hover {
  opacity: 1;
}

.video_wrap {
  width: 100%;
  position: relative;
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
}

.video_wrap iframe,
.video_wrap object,
.video_wrap embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 500;
}

.nofixNav {
  position: static !important;
}

.h1_title {
  width: 100%;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 2px solid #005e91;
  font-size: 34px;
  color: #fff;
}

.h1_title_dark {
  width: 100%;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 2px solid #005e91;
  font-size: 34px;
  color: #5d5d5d;
}

.item_title a {
  display: inline-block;
  width: 100%;
  color: #0071AE;
  text-decoration: none;
}

.item_text {
  color: #5d5d5d;
  font-size: 13px;
}

.project_name {
  position: absolute;
  bottom: 0px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.8);
  transition: .5s;
}

.project_name a {
  text-decoration: none;
  color: #012148;
}

.pageing {
  margin-top: 20px;
  margin-bottom: 30px;
  text-align: center;
  width: 100%;
}

.pageing a {
  font-size: 16px;
  text-decoration: none;
  background: #0071AE;
  color: #fff;
  padding: 5px 7px;
  border-radius: 5px;
}

.pageing a:hover {
  background: #007bff;
}

.pageing a.currentPageing {
  background: #007bff;
}

.inside_aside {
  width: 320px;
}

.inside_aside .inside_aside_scroll {
  width: 100%;
  max-height: 600px;
  overflow-y: scroll;
}

.inside_aside .inside_aside_scroll .inside_aside_item {
  display: flex;
  width: 100%;
  min-height: 135px;
  margin: 5px 0px 15px 0px;
  background: #f7f7f7;
}

.inside_aside .inside_aside_scroll .inside_aside_item .inside_aside_item_cover {
  min-width: 135px;
  max-width: 135px;
  min-height: 135px;
  max-height: 135px;
  margin-right: 7px;
  overflow: hidden;
}

.inside_aside .inside_aside_scroll .inside_aside_item .inside_aside_item_cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.5s;
}

.inside_aside .inside_aside_scroll .inside_aside_item .inside_aside_item_text {
  padding: 5px 0;
}

.inside_aside .inside_aside_scroll .inside_aside_item .inside_aside_item_text h5 a {
  display: inline-block;
  width: 100%;
}

.inside_aside .inside_aside_scroll .inside_aside_item:hover .inside_aside_item_cover img {
  transform: scale(1.1);
}

.inside_aside .inside_aside_scroll .inside_aside_item:hover .inside_aside_item_text h5 a {
  color: #007bff;
  text-decoration: underline;
}

header {
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom: 1px solid #005e91;
}

header .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: row wrap;
}

header .header .header_left {
  display: flex;
}

header .header .header_left .header_logo,
header .header .header_left .radio_btn {
  cursor: pointer;
}

header .header .header_left .header_logo {
  max-width: 70px;
}

header .header .header_left .header_logo img {
  width: 100%;
}

header .header .header_center p {
  margin-bottom: 8px;
  text-align: center;
  color: #fff;
}

header .header .header_right a {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  text-align: center;
  margin: 0 0 0 8px;
  width: 35px;
  font-weight: bold;
  font-size: 14px;
}

header .header .header_right a img {
  width: 100%;
  border-radius: 5px;
  margin-bottom: 5px;
  transition: 0.5s;
}

header .header .header_right a:hover {
  color: lightgray;
}

header .header .header_right a:hover img {
  transform: scale(1.2);
}

