nav {
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom: 1px solid #005e91;
}

nav .nav {
  padding: 0px 10px;
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  align-items: center;
}

nav .nav .menuBtn,
nav .nav .close_menu {
  display: none;
  cursor: pointer;
}

nav .nav .nav_menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 900px;
}

nav .nav .nav_menu li {
  list-style-type: none;
}

nav .nav .nav_menu li a {
  text-decoration: none;
  color: #fff;
  font-size: 17px;
  font-weight: bold;
  padding-bottom: 8px;
}

nav .nav .nav_menu li a:hover {
  border-bottom: 2px solid #fff;
}

nav .nav .search_area {
  display: flex;
  align-items: center;
}

nav .nav .search_area input {
  width: 150px;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #fff;
}

nav .nav .search_area img {
  width: 45px;
  cursor: pointer;
}

nav .nav .video_audio {
  display: flex;
  justify-content: space-between;
}

nav .nav .video_audio a {
  display: flex;
  align-items: center;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  padding: 10px;
}

nav .nav .video_audio a img {
  width: 35px;
  margin-right: 8px;
  transition: 0.5s;
}

nav .nav .video_audio a:hover {
  background: rgba(255, 255, 255, 0.3);
  color: #000;
}

nav .nav .video_audio a:hover img {
  transform: scale(1.2);
}

