main {
  background: #f0efef;
}

main .staff_bg {
  max-height: 400px;
  overflow: hidden;
}

main .staff_bg img {
  width: 100%;
  object-fit: cover;
}

main .fixed_social {
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  position: fixed;
  left: 0;
  top: 250px;
  background: #012148;
  padding: 5px;
  z-index: 555;
  width: 40px;
  min-height: 150px;
}

main .fixed_social a {
  display: block;
  text-decoration: none;
}

main .fixed_social a img {
  width: 30px;
  height: 30px;
  box-shadow: 0px 0px 7px 1px #fff;
  border-radius: 50%;
  transition: 0.5s;
}

main .fixed_social a:hover img {
  transform: scale(1.1);
  box-shadow: 0px 0px 7px 1px #ccc;
}

main .live_top {
  display: flex;
  justify-content: space-between;
}

main .live_top .live_stream {
  width: 900px;
  margin-right: 30px;
}

main .live_top .top_content {
  width: 500px;
}

main .live_top .top_content h2 {
  color: #012148;
}

main .live_top .top_content .top_items {
  margin-top: 10px;
  max-height: 406px;
  overflow: scroll;
  overflow-x: hidden;
}

main .live_top .top_content .top_items .top_item {
  display: flex;
  max-height: 140px;
  overflow: hidden;
  text-decoration: none;
  color: #212121;
  margin-bottom: 10px;
  background: #f7f7f7;
}

main .live_top .top_content .top_items .top_item .top_item_cover {
  min-width: 140px;
  max-width: 140px;
  min-height: 140px;
  max-height: 140px;
  overflow: hidden;
}

main .live_top .top_content .top_items .top_item .top_item_cover img {
  width: 100%;
}

main .live_top .top_content .top_items .top_item .top_item_text {
  margin-left: 20px;
}

main .live_top .top_content .top_items .top_item .top_item_text h3 {
  margin-bottom: 8px;
  color: #012148;
}

main .main_parralax {
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

main .main_parralax .main_items .main_items_content {
  margin: 30px 0;
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  margin-bottom: 20px;
}

main .main_parralax .main_items .main_items_content .main_item {
  margin-top: 10px;
  max-width: 300px;
  overflow: hidden;
  background: #f7f7f7;
}

main .main_parralax .main_items .main_items_content .main_item .main_item_cover {
  width: 100%;
  max-height: 200px;
  overflow: hidden;
}

main .main_parralax .main_items .main_items_content .main_item .main_item_cover img {
  width: 100%;
  transition: 0.5s;
}

main .main_parralax .main_items .main_items_content .main_item .main_item_text {
  padding: 10px;
}

main .main_parralax .main_items .main_items_content .main_item .main_item_text h2 {
  margin-bottom: 5px;
}

main .main_parralax .main_items .main_items_content .main_item .main_item_text h2 a {
  text-decoration: none;
  color: #012148;
}

main .main_parralax .main_items .main_items_content .main_item .main_item_text p {
  color: #5d5d5d;
  font-size: 14px;
}

main .main_parralax .main_items .main_items_content .main_item:hover .main_item_text h2 a {
  color: #007bff;
}

main .main_parralax .main_items .main_items_content .main_item:hover .main_item_cover img {
  transform: scale(1.3);
}

main .main_parralax .main_items .main_items_content .main_video {
  margin-top: 10px;
  width: 24%;
}

main .main_parnters {
  margin: 30px auto;
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
}

main .main_parnters h1 {
  color: #212121;
}

main .main_parnters .partner_logo {
  max-width: 290px;
  max-height: 290px;
  margin-bottom: 10px;
}

main .main_parnters .partner_logo img {
  width: 100%;
