  float: right;
  margin-left: 20px;
}

main .download_section .download_items .download_social .footer_social a img {
  width: 100%;
  box-shadow: 0px 0px 7px 1px #fff;
  border-radius: 50%;
  transition: 0.5s;
}

main .download_section .download_items .download_social .footer_social a:hover img {
