  margin-top: 30px;
}

main .page_section .page_section_items {
  width: 900px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-start;
}

main .page_section .page_section_items .interviews_section_item {
  max-width: 440px;
  min-height: 135px;
  margin: 5px 0px 15px 0px;
  overflow: hidden;
  display: flex;
  background: #f7f7f7;
}

main .page_section .page_section_items .interviews_section_item .interviews_section_item_cover {
  min-width: 135px;
  max-width: 135px;
  min-height: 135px;
  max-height: 135px;
  overflow: hidden;
}

main .page_section .page_section_items .interviews_section_item .interviews_section_item_cover a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.5s;
}

main .page_section .page_section_items .interviews_section_item .interviews_section_item_text {
  margin-left: 7px;
  padding: 5px;
}

main .page_section .page_section_items .interviews_section_item .interviews_section_item_text h4 {
  margin-bottom: 5px;
}

main .page_section .page_section_items .interviews_section_item:hover .interviews_section_item_cover a img {
  transform: scale(1.1);
}

main .page_section .page_section_items .interviews_section_item:hover .interviews_section_item_text h4 a {
  color: #007bff;
  text-decoration: underline;
}

main .page_section .page_section_items .project_section_item {
  max-width: 290px;
  margin: 5px 0px 10px 0px;
  background: #f7f7f7;
